import { MAX_INT } from "./constants";
import { Rng } from "./Rng";
export class ParkMillerRng extends Rng {
    constructor(seed = 0x193a6754) {
        super();
        this.seed = seed;
    }
    setSeed(seed = 0x193a6754) {
        this.seed = seed;
        return this;
    }
    nextInt() {
        this.seed = Math.imul(48271, this.seed) | 0 % MAX_INT;
        return this.seed & MAX_INT;
    }
}
