import { Iter } from "@aicacia/iter";
import { MAX_INT } from "./constants";
export class UniformIntIter extends Iter {
    constructor(rng, min = 0, max = MAX_INT) {
        super(rng);
        this.rng = rng;
        this.min = min | 0;
        this.max = max | 0;
    }
    iter() {
        return new Iter(this);
    }
    nextInt() {
        return this.rng.nextIntInRange(this.min, this.max);
    }
    next() {
        return { done: false, value: this.nextInt() };
    }
}
