import { Iter } from "./Iter";
export class ForEach extends Iter {
    constructor(iter, fn) {
        super(iter);
        this._fn = ([value, index]) => {
            fn(value, index);
            return value;
        };
    }
    next() {
        const next = super.nextWithIndex();
        if (next.done) {
            return next;
        }
        else {
            return { done: false, value: this._fn(next.value) };
        }
    }
}
Iter.prototype.forEach = function forEach(fn) {
    return new ForEach(this, fn);
};
