import { Iter } from "./Iter";
export class Map extends Iter {
    constructor(iter, fn) {
        super(iter);
        this._fn = fn;
    }
    next() {
        const next = super.nextWithIndex();
        if (next.done) {
            return next;
        }
        else {
            const [value, index] = next.value;
            return {
                done: false,
                value: this._fn(value, index),
            };
        }
    }
}
